.banner{
    &-info{
        flex-direction: column;
        justify-content: center;
    }
    &-title{
        font-size: 50px;
    }
    &-subtitle{
        font-size: 27px;
        .text-theme{
            font-size:25px;
        }
    }
    &-team{
        width: 90%;
        &-description{
            font-size: 20px;
        }
    }
}
.history{
    &-databox{
        flex-direction: column;
        &-link{
            font-size: 55px;
            margin:0px;
        }
    }
}
.team{
    &-target{
        &-title{
            font-size: 45px;
        }
        &-info{
            font-size: 25px;
            &-text{
                font-size: 25px;
            }
        }
    }
}
.team-title{
    font-size: 50px;
}
.history{
    &-title{
        font-size: 35px;
    }
    &-tab{
        height: 100%;
        & .tab-panels{
            margin:0px;
            padding: 0px;
            &-text-info{
                width: 100%;
                font-size: 18px;
                margin-bottom: 15px;
                padding:10px;
            }
        }
        & .tab-nav{
            width: 100%;
            float:none;
            margin:0px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            & .size1::after{
                right:156px;
            }
        }
        & *:after{
            display: none;
        }
    }
}
.value-title{
    font-size: 46px;
}
.contact-title{
    font-size: 45px;
}
.value{
    margin-bottom:25px;
    .box-item-text{
        font-size: 28px;
        text-align: left;
    }
    .box-item-subtext{
        font-size: 23px;
    }
    &-footer{
        font-size: 22px;
    }
}
.team{
    padding: 40px 0px ;
}
.contact-about-member p{
    font-size: 22px;
}
.contact-about-member-number-weight{
    font-size: 22px;
}
.section__text_subtitle{
    font-family: "Akrobat";
    font-size: 17px;
}
.tab-panels{
    margin:0px;
    padding: 0px;
}
.team-boximg{
    background-position:center top;
}
.contact-boximg{
    background-position:center top;
}
.contact-about-member{
    text-align:center;
    &::before{
        width: 50%;
        margin: 0 auto;
        text-align:center;
    }
}
.call{
    &-title{
        margin-top: 40px;
    }
    &-description{
        font-size: 21px;
    }
    &-backimg{
        background-position: right;
    }
    &-boxform{
        height: 150px;
        flex-direction: column;
    }
    &-form-input{
        padding-left: 15px;
        margin:5px 0px;
        width:100%;
        line-height: 1;
    }
}
.call-backimg{
    height:75vh;
}