body{
    background:#f2f2f2;
}
.about-case{
    display: flex;
    &-box{
        width: 50%;
        height: 700px;
        background-image:url(../../img/one-case-stokimg.png);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        &::after{
            content:" ";
            display: block;
            width: 100%;
            height: 100%;
            background: radial-gradient(rgba(255, 255, 255, 0.3) 0%, #f2f2f2 90%);
            box-shadow: inset -10px -10px 40px 10px #f2f2f2;
        }
    }
    &-boxcontent{
        position: absolute;
        width: 35%;
        top: 25%;
        left: 5%;
    }
    &-title{
        font-family:"Comfortaa";
        font-size: 48px;
    }
    &-description{
        font-family: 'OpenSans';
        font-size: 18px;
    }
    &-img{
        width: 50%;
    }
}