.banner{
    height: 100vh;
    &-info{
        height: 100vh;
        display: flex;
        align-items: center;
    }
    &-titles{
        width:90%;
    }
    &-title{
        font-family: "OpenSansBold";
        font-size: 90px;
        color: white;
        text-transform: uppercase;
    }
    &-subtitle{
        font-family: "Akrobat";
        font-weight: lighter;
        margin-top: 30px;
        font-size: 36px;
        color: white;
        text-transform: uppercase;
        & .text-theme{
            font-size: 36px;
        }
    }
    &-team{
        &-counter{
            font-size: 60px;
            font-family: "OpenSansBold";
            color: white;
        }
        &-description{
            font-size: 30px;
            color: white;
        }
    }
}
/*Team-target*/
.team-target{
    padding:100px 0px;
    &-title{
        font-family: "OpenSans";
        font-size: 56px;
        text-transform: uppercase;
        font-weight: bold;
        letter-spacing: 2px;
        margin-bottom:35px;
        margin-top: 50px;
    }
    &-info{
        font-size: 32px;
        margin-bottom: 30px;
        &-text{
            color: $theme-color;
            font-size: 32px;
        }
    }
}
/*value*/
.value{
    padding-bottom: 100px;
    &-title{
        text-align: center;
        font-size: 56px;
        font-family: "OpenSansBold";
        text-transform: uppercase;
        margin-top: 100px;
        margin-bottom:25px;
    }
    .box-item{
        margin-bottom: 30px;
        &-link{
            height: 300px;
            background-size: cover;
            display: block;
            width:100%;
            text-decoration: none;
        }
        &-text{
            display: flex;
            align-items: center;
            height: 300px;
            justify-content: center;
            color: white;
            flex-direction: column;
            font-size: 32px;
            font-weight: bold;
            transition: all 300ms;
            text-align: center;
            &:hover{
                color:$theme-color;
            }
            &:hover .box-item-subtext{
                color:$theme-color;
            }
        }
        &-subtext{
            font-size: 26px;
            color: white;
            font-weight: lighter;
            text-align: center;
            transition: all 300ms;
        }
    }
    &-footer{
        text-transform: uppercase;
        text-align: center;
        font-size: 26px;
    }
}

.history{
    margin-top: 90px;
    &-title{
        font-family: "OpenSans";
        font-weight: bold;
        font-size: 56px;
        text-transform: uppercase;
        text-align: center;
        margin-bottom:50px;
    }
    &-databox{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 670px;
        &-link{
            font-size: 80px;
            color: white;
            margin:0 85px;
            font-weight: bold;
            transition:all 300ms;
            &:hover{
                text-decoration:none;
                color:$theme-color;
                border-radius: 50%;
            }
        }
    }
    &-background{
        height: 670px;
        background-size: cover;
    }
    &-tab{
        display: flex;
        display: none;
        align-items: center;
         height: 670px;
        #t1:after {
            content: "2017";
            position: absolute;
            right: 200px;
            line-height: 100px;
            font-size: 225px;
            bottom: 110px;
            opacity: 0.2;
            font-weight: bold;
            left: 660px;
            &:hover{

            }
        }
        #t2:after {
            content: "2016";
            position: absolute;
            right: 200px;
            line-height: 100px;
            font-size: 225px;
            bottom: 110px;
            opacity: 0.2;
            font-weight: bold;
            left: 660px;
        }
        #t3:after {
            content: "2015";
            position: absolute;
            right: 200px;
            line-height: 100px;
            font-size: 225px;
            bottom: 110px;
            opacity: 0.2;
            font-weight: bold;
            left: 660px;
        }
        #t4:after {
            content: "2014";
            position: absolute;
            right: 200px;
            line-height: 100px;
            font-size: 225px;
            bottom: 110px;
            opacity: 0.2;
            font-weight: bold;
            left: 660px;
        }
        #t5:after {
            content: "2013";
            position: absolute;
            right: 200px;
            line-height: 100px;
            font-size: 225px;
            bottom: 110px;
            opacity: 0.2;
            font-weight: bold;
            left: 660px;
        }
        .tab-box{
            width: 100%;
        }
        .tab-box:after{
          clear:both;
          content:"";
          display:block;
        }
        .tab-nav{
          margin:0;
          margin-left: 300px;
          padding:0;
          width: 150px;
          float:left;
        }
        .tab-nav li{
          float:left;
          margin-bottom:1px;
        }
        .tab-nav li a{
          display:inline-block;
          padding:5px 10px;
          text-decoration:none;
          color: black;
          font-weight: bold;
          font-size: 72px;
        }
        .tab-nav li a:hover{
          color:$theme-color;
          text-decoration:none;
        }
        .tab-nav li.select a{
          color: $theme-color;
        }
        .tab-nav .active{
          display:inline-block;
          padding:5px;
        }
        .tab-panels{
            z-index: 1;
            position: relative;
            margin:0 0 0 685px;
            padding:45px 100px;
            padding-right: 250px;
            color:white;
            background:$theme-color;
            &-text{
              color:white;
              &-info{
                  width: 90%;
                  margin-bottom:25px;
                  color: white;
                  font-size: 26px;
              }
              &-year{
                  font-size: 225px;
                  position: absolute;
                  top: 210px;
                  z-index: 1;
                  left: 630px;
                  color: white;
                  opacity: 0.2;
              }
            }
        }
        .tab-nav{
            li.select .size1:after {
                border:solid 10px #99e0cd;
                transition: all 300ms;
            }
            li.select .size2:after {
                opacity: 1;
                border:solid 10px #99e0cd;
                transition: all 300ms;
            }
            li.select .size3:after {
                opacity: 1;
                border:solid 7px #99e0cd;
                transition: all 300ms;
            }
            li.select .size4:after {
                opacity: 1;
                border:solid 7px #99e0cd;
                transition: all 300ms;
            }
            li.select .size5:after {
                opacity: 1;
                border:solid 5px #99e0cd;
                transition: all 300ms;
            }
            &-link{

            }
            .size1{
                position: relative;
                &::after{
                    content:" ";
                    display:inline-block;
                    top: 33%;
                    right: 150px;
                    width:50px;
                    height: 50px;
                    position: absolute;
                    background:$theme-color;
                    border:solid 6px $theme-color;
                    border-radius: 50%;
                }
            }
            .size2{
                position: relative;
                &::after{
                    content:" ";
                    display:inline-block;
                    top: 33%;
                    right: 162.5px;
                    width:40px;
                    height: 40px;
                    position: absolute;
                    background:$theme-color;
                    border:solid 6px $theme-color;
                    border-radius: 50%;
                    opacity: .7;
                }
            }
            .size3{
                position: relative;
                &::after{
                    content:" ";
                    display:inline-block;
                    top: 33%;
                    right: 165px;
                    width:30px;
                    height: 30px;
                    position: absolute;
                    background:$theme-color;
                    border:solid 6px $theme-color;
                    border-radius: 50%;
                    opacity: .6;
                }
            }
            .size4{
                position: relative;
                &::after{
                    content:" ";
                    display:inline-block;
                    top: 33%;
                    right: 167.5px;
                    width:25px;
                    height: 25px;
                    position: absolute;
                    background:$theme-color;
                    border:solid 6px $theme-color;
                    border-radius: 50%;
                    opacity: .4;
                }
            }
            .size5{
                position: relative;
                &::after{
                    content:" ";
                    display:inline-block;
                    top: 33%;
                    right: 169px;
                    width:20px;
                    height: 20px;
                    position: absolute;
                    background:$theme-color;
                    border:solid 6px $theme-color;
                    border-radius: 50%;
                    opacity: .2;
                }
            }
        }
    }
}
.team{
    &-title{
        text-transform: uppercase;
        text-align: center;
        font-family: "OpenSansBold";
        color: black;
        font-size: 56px;
    }
    &-box{

    }
    &-boximg{
        width: 100%;
        height: 465px;
        background-size: cover;
        background-position: center center;
    }
    &-boxmember{
        width:100%;
        height: 590px;
        overflow: hidden;
    }
    .show{
        height: 100%;
    }
    &-member{
        display: block;
        margin-top: 50px;
        text-decoration: none;
        &:hover{
            text-decoration: none;
        }
    }
    .img{
        transform: rotate(180deg);
        transition: all 300ms;
    }
    &-button{
        text-align: center;
    }
    &-name{
        margin-top:10px;
        text-align: center;
        font-size: 30px;
        text-transform: uppercase;
        font-weight: bold;
    }
    &-description{
        text-align: center;
        font-size: 24px;
        line-height: 1;
    }
}
.contact{
    margin-bottom: 70px;
    &-title{
        text-transform: uppercase;
        text-align: center;
        font-family: "OpenSansBold";
        color: black;
        font-size: 56px;
    }
    &-box{
    }
    &-boximg{
        width: 100%;
        height: 465px;
        background-size: cover;
        background-position: center center;
    }
    &-member{
        display: block;
        margin-top: 50px;
        &:hover{
            text-decoration: none;
        }
    }
    &-name{
        margin-top:10px;
        text-align: center;
        font-size: 30px;
        text-transform: uppercase;
        font-weight: bold;
    }
    &-description{
        text-align: center;
        font-size: 24px;
        line-height: 1;
    }
    &-about-member{
        margin-top:25px;
        &::before{
            content:" ";
            border-top:solid 2px $theme-color;
            width: 60%;
            display: block;
        }
        &-number{
            margin-bottom:15px;
            &-weight{
                font-family:"Akrobat";
                font-weight: bold;
            }
        }
    }
}
.call{
    &-title{
        text-align: center;
        color:$theme-color;
        font-size: 48px;
        text-transform: uppercase;
        margin-top:75px;
        margin-bottom:5px;
    }
    & form{
        text-align: center;
    }
    &-backimg{
        padding: 25px;
        height: 65vh;
        background-repeat: no-repeat;
        background-size: cover;
    }
    &-boxform{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 120px;
    }
    &-description{
        font-size: 26px;
        color: white;
        text-align: center;
        margin-bottom: 20px;
    }
    &-form-input{
        border:solid 1px grey;
        padding-left: 25px;
        background:transparent;
        width:500px;
        height: 55px;
        margin:0 5px;
        color: white;
        &:focus{
            outline:0;
            border:solid 1px $theme-color;
        }
        &::placeholder{
            letter-spacing:.5px;
            color: #d6d6d6;
        }
    }
    &-form-button{
        margin-top:10px;
        width:200px;
        height: 50px;
        text-align: center;
        border:$theme-color;
        background:$theme-color;
        color:white;
        font-family: "OpenSans";
        font-size: 18px;
        font-weight: bold;
        letter-spacing: 1px;
    }
}
.footer{
    display:block;
}
@media screen and (max-width:1440px){
    .history-tab .tab-panels{
        z-index: 1;
        position: relative;
        margin: 0 0 0 685px;
        padding: 15px 20px;
        padding-right: 50px;
        color: white;
        background: #02a383;
    }
    .history-tab #t1:after{
        left: 40%;
    }
    .history-tab #t2:after{
        left: 40%;
    }
    .history-tab #t3:after{
        left: 40%;
    }

    .history-tab #t4:after{
        left: 40%;
    }
    .history-tab #t5:after{
        left: 40%;
    }
    .call-backimg{
        height:75vh;
    }
}
